.tradeButtonsContainer {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: center;
    height: 28px;
    margin-left: 2px;
    margin-right: 4px;
}

.headerButtonSmall {
    width: 22px;
    height: 13px;
    border-radius: 10px;
    cursor: pointer;

    background: rgba(70, 70, 70, 0.7);
}

.headerButtonSmall:hover {
    background: rgba(90, 90, 90, 0.7);
}

.headerButtonSmall:active {
    background: rgba(30, 30, 30, 0.7);
}

.name {
    display: flex;
    flex-flow: row nowrap;
    width: 30px;
    justify-content: right;
    white-space: nowrap;
}

.description {
    display: flex;
    flex-flow: row nowrap;
    width: 30px;
    justify-content: left;
    white-space: nowrap;
    padding-left: 5px;
}