.modalBackground {
    position: fixed;
    top: 50%;
    left: 0;
    width: 100vw;
    height: 0;

    z-index: 21;

    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContainer {
    border: 1px solid green;

    z-index: 22;

    max-width: 960px;
    width: 90%;
    min-width: 380px;

    max-height: calc(100vh - 120px);

    border-radius: 10px;
    overflow: hidden;

    padding: 5px;
    background: rgba(40, 40, 40, 1);
}

.modalFooter {
    width: max-content;
    margin: 10px auto 5px auto;
    text-align: center;
}

