.body {
    gap: 4px;
}

.body_hover {
    border-radius: 10px;
    cursor: pointer;
}

.body_hover:hover {
    background: rgba(70, 70, 70, 1);
}

.modal_content {
    justify-content: space-around;
    flex-flow: column;
    gap: 5px;
}

.settingsHeader {
    margin-bottom: 5px;
}

.avatar {
    width: 60px;
    height: 60px;
    border: rgba(90, 90, 90, 0.7) solid 2px;
    border-radius: 100%;
}

.avatarSmall {
    width: 22px;
    height: 22px;
}